// js/controlsManager.js
import { MicroMapRenderer } from './microMapRenderer.js';

class ControlsManager {
    constructor(simulationCore, renderer, mainCanvas, microMapCanvas, CELL_PIXEL_SIZE, GRID_RESOLUTION) {
        this.simulationCore = simulationCore;
        this.renderer = renderer;
        this.mainCanvas = mainCanvas;
        this.microMapCanvas = microMapCanvas;
        this.CELL_PIXEL_SIZE = CELL_PIXEL_SIZE; // Pass these constants
        this.GRID_RESOLUTION = GRID_RESOLUTION;

        this.hoveredSettlement = null;
        this.animationFrameId = null;

        this._setupEventListeners();
        this._setupCanvasHover();
    }

    _setupEventListeners() {
        document.getElementById('resetBtn').addEventListener('click', () => this.resetSimulation());

        const rainSlider = document.getElementById('rainIntensitySlider');
        const rainValueSpan = document.getElementById('rainValue');
        rainSlider.addEventListener('input', (e) => {
            this.simulationCore.setRainfallAmount(parseFloat(e.target.value));
            rainValueSpan.textContent = this.simulationCore.RAINFALL_AMOUNT.toFixed(3);
        });

        const cloudinessSlider = document.getElementById('cloudinessSlider');
        const cloudinessValueSpan = document.getElementById('cloudinessValue');
        cloudinessSlider.addEventListener('input', (e) => {
            this.simulationCore.setCloudinessFactor(parseFloat(e.target.value));
            cloudinessValueSpan.textContent = this.simulationCore.CLOUDINESS_FACTOR.toFixed(2);
        });

        const flowSlider = document.getElementById('flowRateSlider');
        const flowValueSpan = document.getElementById('flowValue');
        flowSlider.addEventListener('input', (e) => {
            this.simulationCore.setFlowRateFactor(parseFloat(e.target.value));
            flowValueSpan.textContent = this.simulationCore.FLOW_RATE_FACTOR.toFixed(3);
        });

        const infiltrationSlider = document.getElementById('infiltrationRateSlider');
        const infiltrationValueSpan = document.getElementById('infiltrationValue');
        infiltrationSlider.addEventListener('input', (e) => {
            this.simulationCore.setBaseInfiltrationRate(parseFloat(e.target.value));
            infiltrationValueSpan.textContent = this.simulationCore.BASE_INFILTRATION_RATE.toFixed(4);
        });

        const toggleClouds = document.getElementById('toggleClouds');
        toggleClouds.addEventListener('change', (e) => {
            this.renderer.setShowClouds(e.target.checked);
            // No need to set on simulationCore unless simulation logic depends on it
        });

        document.querySelectorAll('input[name="visualizationMode"]').forEach(radio => {
            radio.addEventListener('change', (e) => {
                this.renderer.setVisualizationMode(e.target.value);
            });
        });
    }

    _setupCanvasHover() {
        // Mostrar micro-mapa solo al hacer clic
        this.mainCanvas.addEventListener('click', (e) => {
            const rect = this.mainCanvas.getBoundingClientRect();
            const scaleX = this.mainCanvas.width / rect.width;
            const scaleY = this.mainCanvas.height / rect.height;
            const mouseX = (e.clientX - rect.left) * scaleX;
            const mouseY = (e.clientY - rect.top) * scaleY;
            const gridX = Math.floor(mouseX / this.CELL_PIXEL_SIZE);
            const gridY = Math.floor(mouseY / this.CELL_PIXEL_SIZE);
            let selected = null;
            const settlements = this.simulationCore.settlementManager.getSettlements();
            for (const s of settlements) {
                if (gridX >= s.x && gridX < s.x + s.size && gridY >= s.y && gridY < s.y + s.size) {
                    selected = s;
                    break;
                }
            }
            this.hoveredSettlement = selected;
            const sideContainer = document.getElementById('sideMicroMapContainer');
            if (selected) {
                sideContainer.style.display = 'block';
                this.microMapCanvas.style.display = 'block';
                new MicroMapRenderer(this.microMapCanvas).drawMicroMap(
                    selected,
                    this.simulationCore.getMaps(),
                    this.mainCanvas.width,
                    this.CELL_PIXEL_SIZE,
                    this.GRID_RESOLUTION
                );
            } else {
                sideContainer.style.display = 'none';
                this.microMapCanvas.style.display = 'none';
            }
        });
        // Botón cerrar
        document.getElementById('closeMicroMapBtn').onclick = () => {
            document.getElementById('sideMicroMapContainer').style.display = 'none';
            this.microMapCanvas.style.display = 'none';
            this.hoveredSettlement = null;
        };
    }

    animate() {
        this.simulationCore.updateSimulation();
        this.renderer.draw(this.simulationCore.getMaps(), this.simulationCore.settlementManager.getSettlements(), {
            cloudinessFactor: this.simulationCore.CLOUDINESS_FACTOR,
            baseInfiltrationRate: this.simulationCore.BASE_INFILTRATION_RATE,
            showClouds: this.renderer.showClouds
        });
        const sideContainer = document.getElementById('sideMicroMapContainer');
        if (sideContainer.style.display !== 'none' && this.hoveredSettlement) {
            new MicroMapRenderer(this.microMapCanvas).drawMicroMap(
                this.hoveredSettlement,
                this.simulationCore.getMaps(),
                this.mainCanvas.width,
                this.CELL_PIXEL_SIZE,
                this.GRID_RESOLUTION
            );
        }
        this.animationFrameId = requestAnimationFrame(() => this.animate());
    }

    startSimulation() {
        if (!this.animationFrameId) { // Prevent multiple animation loops
            this.animate();
        }
    }

    resetSimulation() {
        if (this.animationFrameId) {
            cancelAnimationFrame(this.animationFrameId);
            this.animationFrameId = null;
        }
        this.hoveredSettlement = null;
        this.microMapCanvas.style.display = 'none';
        this.simulationCore.resetSimulation();
        // Redraw immediately after reset
        this.renderer.draw(this.simulationCore.getMaps(), this.simulationCore.settlementManager.getSettlements(), {
            cloudinessFactor: this.simulationCore.CLOUDINESS_FACTOR,
            baseInfiltrationRate: this.simulationCore.BASE_INFILTRATION_RATE,
            showClouds: this.renderer.showClouds
        });
        this.startSimulation(); // Restart the animation loop
    }
}

export { ControlsManager };